/* Base styles */
body {
  font-family: 'Titillium Web', sans-serif;
  font-size:16px;
  min-width: 320px;
  color: #fff;
}

*{
  box-sizing: border-box;
}
a{
  color: #fff;
  &:hover{
    color: #fff;
  }
}
a, a:hover{
  text-decoration: none;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}



// Custom styles here
header {
  min-height: 596px;
  width: 100%;
  height: 100%;

}

header{
  background-color: #87509C;
}

.logo{
  display: inline-block;
  float: left;
}

.m-t-container{
  padding-top: 57px;
  padding-bottom: 146px;
}

.large{
  float: right;
  line-height: 37px;
  a{
    font-weight: 400;
    vertical-align:middle;
    color: #fff;
    text-transform: uppercase;
    padding:11px 19px;
    border-radius: 3px;
    &:hover{
      color: #fff;
      background-color: #643A79;
    }
  }
}
.small{
  display: none;
  float: right;
  transition: 0.3s all linear;
  position: relative;
  i{
    line-height: 37px;
  }
  &:hover{
    color: #EB7D4B;
    cursor: pointer;
  }

}
.small:hover .nav_menu{
  visibility: visible;
}
.nav_menu{
  visibility: hidden;
  position: absolute;
  z-index: 5;
  right: 30px;
  top: 8px;
  display: block;
  float: right;
  background-color: #1a2c51;
  border-radius: 3px;
  li{
    border-bottom:1px solid #000000;
    padding: 10px 40px;
    text-transform: uppercase;
    transition: 0.3s all linear;
    &:hover{
      background-color: #DE78EB;
      border-radius: 3px;
    }
    &:last-child{
      border-bottom:none;
    }
  }
}

.visibility{
  visibility: visible;
}
h1{
  text-align: center;
  font-size: 42.16px;
  line-height:48.82px;
  white-space: pre-line;
  margin-bottom: 62px;
  margin-top: 0;
  font-weight:bold;
}
.btn{
  padding: 0;
  border: 0;
  margin: auto;
  display: block;
  padding-bottom: 40px;
}
.btn a{
  text-transform: uppercase;
  font-weight: 600;
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
  line-height: 66px;
  display: block;
  border-radius: 4px;
  background-color: rgb(235, 125, 75);
  width: 100%;
  max-width: 293px;
  height: 66px;
  position: relative;
  border-bottom: 4px solid rgba(0, 0, 0, 0.149);
}

////// Two
