    text-align: center;
    -moz-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
    -webkit-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
    -ms-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
    border-bottom: 4px solid rgba(0, 0, 0, 0.149);
    border-radius: 4px;
    background-color: rgb(23, 194, 164);
    width: 293px;
    height: 66px;
    margin-bottom: 40px;
    margin-top: 10px;
  }

}

// tabs-----------------
.responsive-tabs {
  font-weight: 400;
  font-size: 18px;
  line-height: 19.5;
  color: #393939;
  width: 378px;
  margin:auto;
  text-align: center;
  margin-bottom: 20px;
}
.responsive-tabs dt {
  text-transform: uppercase;
  padding: 0.5em;
  cursor: pointer;
  background-color: #ebc985;
  width: 84px;
  height: 38px;
  border-radius: 3px;
  margin:* 5px;
}
.responsive-tabs dt:first-child {
  border-top: 0;
}
.responsive-tabs .active {
  background-color: #fff;
}
.responsive-tabs .active + dd {
  display: block;
}
.responsive-tabs dd {
  display: none;
  padding: 0.5em;
}

.responsive-tabs-placeholder {
  display: none;
}


@media only screen and (min-width: 768px) {

  .responsive-tabs {

  }
  .responsive-tabs dt {
    float: left;
    border-top: 0;
  }
  .responsive-tabs .active + dd {
    display: none;
  }

  .responsive-tabs-placeholder {
    clear: both;
    display: block;
  }

}
@media only screen and (max-width: 768px) {
  .responsive-tabs dt {
    width: 100%;
    margin-bottom: 10px;
  }
  .portfolio{
    .btn_more{
      width: 200px;
    }
  }
}

