/* Base styles */

body {
  font: 16px 'Titillium Web', sans-serif;
  min-width: 320px;
  color: #fff;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  color: #fff;
}

a:hover {
  color: #fff;
}

a,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

header {
  min-height: 596px;
  width: 100%;
  height: 100%;
}

header {
  background: #87509C;
}

.logo {
  display: inline-block;
  float: left;
}

.m-t-container {
  padding-top: 57px;
  padding-bottom: 146px;
}

.large {
  float: right;
  line-height: 37px;
}

.large a {
  padding: 11px 19px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  color: #fff;
  vertical-align: middle;
  text-transform: uppercase;
  font-weight: 400;
}

.large a:hover {
  background: #643A79;
  color: #fff;
}

.small {
  position: relative;
  display: none;
  float: right;
  -webkit-transition: 0.3s all linear;
  transition: 0.3s all linear;
}

.small i {
  line-height: 37px;
}

.small:hover {
  color: #EB7D4B;
  cursor: pointer;
}

.small:hover .nav_menu {
  visibility: visible;
}

.nav_menu {
  background: #1a2c51;
  position: absolute;
  top: 8px;
  right: 30px;
  z-index: 5;
  display: block;
  visibility: hidden;
  float: right;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}

.nav_menu li {
  padding: 10px 40px;
  border-bottom: 1px solid #000000;
  text-transform: uppercase;
  -webkit-transition: 0.3s all linear;
  transition: 0.3s all linear;
}

.nav_menu li:hover {
  background: #DE78EB;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}

.nav_menu li:last-child {
  border-bottom: none;
}

.visibility {
  visibility: visible;
}

h1 {
  margin-top: 0;
  margin-bottom: 62px;
  text-align: center;
  white-space: pre-line;
  font-weight: 700;
  font-size: 42.16px;
  line-height: 48.82px;
}

.btn {
  display: block;
  margin: auto;
  padding: 0;
  padding-bottom: 40px;
  border: 0;
}

.btn a {
  background: rgb(235, 125, 75);
  position: relative;
  display: block;
  margin: 0 auto;
  max-width: 293px;
  width: 100%;
  height: 66px;
  border-bottom: 4px solid rgba(0, 0, 0, 0.149);
  -webkit-border-radius: 4px;
  border-radius: 4px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
  line-height: 66px;
}

.services {
  background: #17C2A4;
  min-height: 596px;
  width: 100%;
  height: 100%;
}

.services h2 {
  margin: 0;
  padding-top: 119px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 40px;
  line-height: 29.91px;
}

.services h2:after {
  background: rgb(0, 0, 0);
  display: block;
  margin: 0 auto;
  margin-top: 22px;
  margin-bottom: 24px;
  width: 87px;
  height: 4px;
  content: '';
  opacity: 0.149;
}

.services p {
  margin-bottom: 82px;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
}

.services img {
  margin-right: auto;
  margin-bottom: 62px;
  margin-left: auto;
}

.services h3,
.services p {
  text-align: center;
}

.services .row h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 18px;
}

.services .row p {
  margin-top: 28px;
  padding-right: 30px;
  padding-left: 30px;
  font-size: 16px;
  line-height: 24px;
}

.lines {
  white-space: pre-line;
}

.team {
  background: #E7F1F8;
  min-height: 914px;
  width: 100%;
  color: #3c4761;
}

.team h2 {
  margin: 0;
  padding-top: 120px;
  text-align: center;
  font-weight: 700;
  font-size: 41.29px;
  line-height: 30.87px;
}

.team h2:after {
  background: rgb(189, 209, 223);
  display: block;
  margin: 0 auto;
  margin-top: 23px;
  margin-bottom: 25px;
  width: 87px;
  height: 6px;
  content: '';
  opacity: 0.749;
}

.team .row img {
  display: block;
  margin: auto;
  margin-bottom: 41px;
}

.team .row h4 {
  margin-bottom: 16px;
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  line-height: 18px;
}

.team .row p {
  font-size: 16px;
  line-height: 18px;
}

.team .row p:first-of-type {
  margin-bottom: 29px;
  color: #30bae7;
  text-align: center;
  font-weight: 400;
}

.team .row p:last-of-type {
  margin-bottom: 34px;
  padding-right: 32px;
  padding-left: 32px;
  text-align: center;
  font-weight: 300;
}

.team-p {
  margin-bottom: 79px;
  text-align: center;
  font-weight: 400;
  font-size: 18.58px;
  line-height: 26.84px;
}

.social {
  margin-bottom: 40px;
  text-align: center;
}

.social i {
  background: #bdd1df;
  margin-right: 6px;
  margin-left: 6px;
  width: 32px;
  height: 32px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  color: #ffffff;
  text-align: center;
  line-height: 32px;
}

.skills {
  background: #fff;
  min-height: 655px;
  width: 100%;
  color: #3c4761;
}

.skills h2 {
  padding-top: 117px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 40px;
  line-height: 29.91px;
}

.skills h2:after {
  background: rgb(223, 232, 237);
  display: block;
  margin: 0 auto;
  margin-top: 22px;
  margin-bottom: 25px;
  width: 87px;
  height: 4px;
  content: '';
}

.skills p {
  margin-bottom: 88px;
  text-align: center;
  white-space: pre-line;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
}

.skills h4 {
  margin-top: 32px;
  margin-bottom: 40px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 24px;
  line-height: 18px;
}

.web,
.html,
.design,
.ui {
  position: relative;
  display: inline-block;
  margin: 20px 10px;
  color: #3c4751;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 16px;
  font-size: 50px;
  line-height: 20.84px;
}

.web span,
.html span,
.design span,
.ui span {
  font-size: 25px;
}

.web canvas,
.html canvas,
.design canvas,
.ui canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.easyPieChart {
  display: block;
  margin: auto;
}

.portfolio {
  background: #FFDD99;
  min-height: 1420px;
}

.portfolio h2 {
  padding-top: 120px;
  color: #393939;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 40px;
  line-height: 29.91px;
}

.portfolio h2:after {
  background: rgb(229, 195, 127);
  display: block;
  margin: 0 auto;
  margin-top: 22px;
  margin-bottom: 24px;
  width: 87px;
  height: 4px;
  content: '';
}

.portfolio p {
  margin-bottom: 44px;
  color: #393939;
  text-align: center;
  white-space: pre-line;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
}

.portfolio dt {
  margin-bottom: 65px;
}

.portfolio img {
  margin: auto;
  margin-bottom: 29px;
  width: 100%;
}

.portfolio img + p {
  margin-bottom: 65px;
  font-weight: 400;
  font-size: 18px;
  line-height: 19.5px;
}

.portfolio .Load_More_Projects_btn {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 73;
  width: 1440px;
  height: 6160px;
}

.portfolio .btn_more {
  background: rgb(23, 194, 164);
  margin: 10px auto 40px;
  width: 293px;
  height: 66px;
  border-bottom: 4px solid rgba(0, 0, 0, 0.149);
  -webkit-border-radius: 4px;
  border-radius: 4px;
  color: rgb(255, 255, 255);
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
  line-height: 70px;
  -moz-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
  -webkit-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
  -ms-transform: matrix( 1.34293269230769,0,0,1.34293269230769,0,0);
}

.responsive-tabs {
  margin: auto;
  margin-bottom: 20px;
  width: 378px;
  color: #393939;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 19.5;
}

.responsive-tabs dt {
  background: #ebc985;
  margin-right: 5px;
  margin-left: 5px;
  padding: 0.5em;
  width: 84px;
  height: 38px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  text-transform: uppercase;
  cursor: pointer;
}

.responsive-tabs dt:first-child {
  border-top: 0;
}

.responsive-tabs .active {
  background: #fff;
}

.responsive-tabs .active + dd {
  display: block;
}

.responsive-tabs dd {
  display: none;
  padding: 0.5em;
}

.responsive-tabs-placeholder {
  display: none;
}

@media only screen and (min-width: 768px) {


  .responsive-tabs dt {
    float: left;
    border-top: 0;
  }

  .responsive-tabs .active + dd {
    display: none;
  }

  .responsive-tabs-placeholder {
    display: block;
    clear: both;
  }
}

@media only screen and (max-width: 768px) {
  .responsive-tabs dt {
    margin-bottom: 10px;
    width: 100%;
  }

  .portfolio .btn_more {
    width: 200px;
  }
}

